/**
 * 
 * @param aCanvasDom dom
 * @param aPaper paper
 * @returns {{setViewportDrag: setViewportDrag}}
 * @constructor
 */
var Viewport = function(aCanvasDom, aPaper){
    var canvasDom = aCanvasDom;
    var paper = aPaper;
    var scale = 1.0;    //1.0
    var canvasWidth = canvasDom.clientWidth || 400;        //
    var canvasHeight = canvasDom.clientHeight || 400;      //
    var viewBox = {
        x: 0,
        y: 0,
        width: canvasWidth,
        height: canvasHeight
    };

    var dragging = false;
    var lastX = 0;
    var lastY = 0;
    var dX, dY, realScale;


    /**
     * svg
     * @param x x
     * @param y y
     * @param aScale 
     */
    function _setViewport(x, y ,aScale){
        scale = aScale;
        var realScale = 1.0 / scale;
        if(scale > 5){
            scale = 5;
        }
        //
        if(scale < 0.2){
            scale = 0.2;
        }

        //
        viewBox.x = x;
        viewBox.y = y;
        viewBox.width = canvasWidth * realScale;
        viewBox.height = canvasHeight * realScale;

        //
        try {
            paper.setViewBox(viewBox.x, viewBox.y, viewBox.width, viewBox.height);
        } catch(e) {
            // TODO  ?
        }
    }

    function mousedownHandle(event){
        realScale = 1.0 / scale;

        lastX = event.layerX;
        lastY = event.layerY;
        dragging = true;
    }

    function mousemoveHandle(event){
        if(dragging){
            dX = -(event.layerX - lastX) * realScale;
            dY = -(event.layerY - lastY) * realScale;

            viewBox.x += dX;
            viewBox.y += dY;

            _setViewport(viewBox.x, viewBox.y, scale);

            lastX = event.layerX;
            lastY = event.layerY;

            //console.log(viewBox.x, viewBox.y);
        }
    }

    function mouseupHandle(event){
        if(dragging){
            dragging = false;
        }
    }

    function setViewportScale(aScale) {
        // let offsetX = (aScale - scale) / 2 * viewBox.width;
        // let offsetY = (aScale - scale) / 2 * viewBox.height;
        _setViewport(viewBox.x, viewBox.y, aScale);
    }

    /**
     * 
     */
    function setViewportDrag(){
        //
        canvasDom.addEventListener('mousedown', function(event){
            if(event.target.nodeName !== 'svg') { return };
            mousedownHandle(event);
            /*
                realScale = 1.0 / scale;

                lastX = event.layerX;
                lastY = event.layerY;
                dragging = true;*/
        });

        //
        canvasDom.addEventListener('mousemove', function(event){
            if(event.target.nodeName !== 'svg') { return };
            mousemoveHandle(event);
            /*
                if(dragging){
                dX = -(event.layerX - lastX) * realScale;
                dY = -(event.layerY - lastY) * realScale;

                viewBox.x += dX;
                viewBox.y += dY;

                _setViewport(viewBox.x, viewBox.y, scale);

                lastX = event.layerX;
                lastY = event.layerY;
                }*/
        });

        //
        canvasDom.addEventListener('mouseup', function(event){
            if(event.target.nodeName !== 'svg') { return };
            mouseupHandle(event);
            /*
                if(dragging){
                dragging = false;
                }*/
        });
    }


    return {
        isDragging: function(){
            return dragging;
        },
        setViewportDrag: setViewportDrag,
        setViewportScale: setViewportScale,
        getViewbox: function(){
            return viewBox;
        },
        mousedownHandle: mousedownHandle,
        mouseupHandle: mouseupHandle,
        mousemoveHandle: mousemoveHandle
    };
};

export default Viewport;

